@import '../../styles/vars';

.menu {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  box-sizing: border-box;
  padding: 24px 10px;
  width: 100vw;
  height: 100vh;

  background-color: $bg-gray;

  transform: translateX(100%);
  transition: transform 500ms;

  &--open {
    transform: translateX(0);
  }  
}
